<template>
    <div class="content" :class="{'blur-content': showDialog}">
      <CCard>
        <CCardBody>
          <CRow>
              <c-col sm="12" align="left"><h4>MANAJEMEN AKSES</h4></c-col>
          </CRow>
          <CRow>
              <c-col sm="12">
                <md-table v-model="data" md-card @md-selected="onSelect">
                  <md-table-row slot="md-table-row" slot-scope="{ item }" >
                    <md-table-cell md-label="ID" md-sort-by="id">{{ item.id }}</md-table-cell>
                    <md-table-cell md-label="PERMISSION" md-sort-by="permission">{{ item.permission }}</md-table-cell>
                    <md-table-cell md-sort-by="id">
                      <md-menu md-direction="bottom-start">
                        <md-button md-menu-trigger><md-icon>more_vert</md-icon></md-button>
                        <md-menu-content>
                          <md-menu-item @click="openDialog">Edit</md-menu-item>
                          <md-menu-item>Non Aktifkan</md-menu-item>
                        </md-menu-content>
                      </md-menu>
                    </md-table-cell>
                  </md-table-row>
                </md-table>
              </c-col>
          </CRow>
          <CRow>
            <c-col align="right"> 
              <md-button class="md-fab md-success" @click="openDialog" style="background-color:green;margin-top:3rem;">
                <md-icon>add</md-icon>
              </md-button>
            </c-col>
          </CRow>
        </CCardBody>
      </CCard>

      <md-dialog :md-active.sync="showDialog" :md-fullscreen="true" :md-backdrop="false">
        <md-dialog-title>TAMBAH PERMISSION</md-dialog-title>

        <md-dialog-content>
        <CRow>
          <CCol sm="12 input-box">
            <md-field>
              <md-input placeholder="Nama Permission"></md-input>
            </md-field>
          </CCol>
        </CRow>
        <CRow>
          <CCol sm="6" align="left">
            <md-button class="md-raised">SIMPAN</md-button>
          </CCol>
          <CCol sm="6" align="right" @click="batalDialog">
            <md-button>BATAL</md-button>
          </CCol>
        </CRow>
        </md-dialog-content>

      </md-dialog>

    </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'AdvancedTables',
  data () {
    return {
      showDialog: false,
      selected: [],
      data: []
    }
  },
  mounted(){
    axios.get(`../data.json`).then(response => {
      this.data = response.data.data_manajemen_akses.data
    })
  },
  methods: {
    onSelect (items) {
      this.selected = items
    },
    getAlternateLabel (count) {
      let plural = ''

      if (count > 1) {
        plural = 's'
      }

      return `${count} user${plural} selected`
    },
    openDialog(){
      this.showDialog=true
      this.$store.commit('toggleSidebarDesktop')
    },
    batalDialog(){
      this.showDialog=false
      this.$store.commit('toggleSidebarDesktop')
    }
  }
}
</script>